import React,{useState} from 'react';
import "./Home.css"
import Typical from 'react-typical'
import Loader from 'react-loader-spinner'
import bg from "../../Images/bg.svg"
import logod from "../../Images/redlogo.svg"
import logo from "../..//Images/logo.svg"
import imgloader from "image-preloader"
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

export default function Body(){

    const [loadedimage, setloadedimage] = useState(false)
    var preloader = new imgloader();
    preloader.preload(bg, logod, logo)
        .then(function(status){
          //  console.log('all done!', status);
            setloadedimage(true)
        });
    
    var bgstyle = {
         backgroundImage: `url(${bg})`
    }


    const [load, setload]  = useState(true)
    const [isvalid, setisvalid]  = useState(true)
    const [thankyou, setthankyou] = useState(false)
    const [label, setlabel] = useState("it seems the email address is in-correct")

    function loadthankyou(r){
     //   console.log(r)
        setthankyou(true)
    }

    function notvalid(){
        setisvalid(false)
    }

    function loading(){
        setload(false);
        post();
    }
    async function post(){
            let url = `https://smcsoon.herokuapp.com/subscribe/${useremail}`;
            //console.log(useremail)
            try {              
                let res = await fetch(url);
                return await loadthankyou(res);
            } catch (error) {
              //  console.log(error);
              loadthankyou("res")
            }
    }
    function validate(mail){
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      //  console.log(re)
        return re.test(String(mail).toLowerCase());
    }
    let useremail="";
    function isvalidemail(){
        if (validate(emailaddress.current.value)){
            // correct address
            useremail = emailaddress.current.value;
            loading()
            setlabel("") // cleare the label
        }else{
            notvalid()
        }
    }
    //const [email,setemail] = useState("");

    let emailaddress = React.createRef();

    const TypingAnimation =  React.memo(()=>{
        return <Typical
                    steps={[
                        'A Composer?', 5000,
                        'A Conductor?', 5000,
                        'A Musician?', 5000,
                        'A Teacher?', 5000
                    ]}
                    loop={Infinity}
                    wrapper="p"
                />
    },(props,prevProp)=> true ); // this line prevent re rendering

    return(
        <div> 
            <div id= {loadedimage ? "hideloading" : "loadingicon"} ><Loader type="Rings" color="#fffbd4" height={120} width={120} /></div>
            <div id= {loadedimage ? "home" : "hiddengen"}>
                <div  id = "bg">
                    <img alt = "Sheetmusicafrica" className = "noSelect" id = "rlogoleft" src = {logod}/>
                    <img alt = "sheetmusicafrica" className = "noSelect" id = "rlogoright" src = {logod}/>
                </div>
                <div id = "popupcon" className = {thankyou ?"show" : "hide"}>
                <div id = "popup">Thank You</div>
                </div>
                <div id = "main_body">
                    <div id = "mobile-logo">
                        <div id = "m-comingsoon">Coming Soon</div>
                        <img alt = "logo" className = "noSelect" src = {logo}/>
                    </div>
                <div className = "noSelect" id = "comingsoon"> Coming Soon</div> 
                <div id = "middle-section">
                    <div  className = "left"> 
                        <div id = "smalogocon">
                                <div id = "polyshape" style = {bgstyle}>
                                    <img alt="logo" className = "noSelect" src = {logo}/>
                                </div>                       
                            </div>
                    </div>
                    <div className = "right" >
                        <div className = "noSelect" id = "jointhewaitcon">
                        <div className = "noSelect" id ="title">Are You </div>
                        <div className = "noSelect" id = "title2"> 
                                <TypingAnimation />
                        </div>
                        <div id = "emailcon">
                            <input ref = {emailaddress} type = "email" placeholder = "John@example.com" />
                            <div id = "emailcondiv" onClick = {isvalidemail} className = {`noSelect ${load ? "" : "reduce" }`}>{ load ? "Stay Updated" : <Loader type="ThreeDots" color="#e3614d" height={8} width={80} />}</div>
                                <div id = {`${isvalid? "displaynone" : "validate"}`} >{label}</div>
                        </div>
                    </div>
                    </div>
                    
                    
                </div>
                <div className = "noSelect" id = "poweredbytx">Powered By TrinityX</div> 
                </div>
            </div>
        </div>
    )
}